<template>
    <div class="header-menu">
        <Menu mode="horizontal" theme="dark" active-name="1">
            <div class="layout-logo">COMPASS</div>
            <div class="layout-nav">
                <MenuItem name="1">
                    <Icon type="ios-navigate"></Icon>
                    Item 1
                </MenuItem>
                <MenuItem name="2">
                    <Icon type="ios-keypad"></Icon>
                    Item 2
                </MenuItem>
                <MenuItem name="3">
                    <Icon type="ios-analytics"></Icon>
                    Item 3
                </MenuItem>
                <MenuItem name="4">
                    <Icon type="ios-paper"></Icon>
                    Item 4
                </MenuItem>
            </div>
        </Menu>
    </div>
</template>

<script>
    export default {
        
    }
</script>

<style lang="less" scoped>
    .header-menu{    

        .ivu-menu-dark{
            background-color: #272c30;
        }

        .layout-logo{
            position: relative;
            top: 15px;
            left: 20px;
            float: left;
            width: 100px;
            height: 30px;
            line-height: 30px;
            text-align: center;
            color: #fff;
            background: #5b6270;
            border-radius: 3px;
        }

        .layout-nav{
            width: 420px;
            margin: 0 auto;
            margin-right: 20px;
        }
    }
</style>



